@font-face {
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  font-weight: 100;
  src: local("Inter Thin"), local("Inter-Thin"),
    url("./fonts/WOFF2/Inter-Thin.woff2") format("woff2"),
    /* Super Modern Browsers */ url("./fonts/WOFF/Inter-Thin.woff")
      format("woff"); /* Modern Browsers */
}

